.projectsBlock {
    min-height: 100vh;
    background-color: grey;
}
.projectsContainer {
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
}
.projectBlock {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    box-sizing: border-box;
}
.title {
    background-color: white;
}